module.exports = {"aboutMe1":"My love for programming has been evolving since I started my studies in college to receive my Bachelor's Degree in Computer Engineering. It began with programming microcontrollers and embedded systems while focusing on hardware, moved on to software product design in my final year of undergradute studies with a major emphasis on software, and now web development where I guess you can say I've completely switched sides and haven't wanted to stop since.","aboutMe2":"Web development is something I'm completely invested in now and will continue to pursue moving forward. I love being able to use my creativity along with my passion for programming web and mobile interfaces to build and develop projects for a better tomorrow.","homeCard1":"Recently taken a keen interest in web development and making the internet look a little more exciting.","homeCard2":"Building more than just websites with basically anything that sparks my interest at the time.","homeCard3":"I think 14 years of playing guitar has earned me the right to say I'm pretty into music, no?","homePanel1":"Basically living on my computer, I spend my time focusing on becoming a web developer nowadays. My degree in computer engineering introduced me to everything from the hardware to software side but pure code is what caught my eye the most in the end. There's just something intriguing about being in complete control of every little detail for a website. To find out more about the work that I have done in the past and am currently working on, check out the <a href='/#/professional'>professional</a> section.","homePanel2":"I can't get through the day without listening to music and I'm always trying to expand my music library. Playing guitar is my favorite thing to do to wind down and is a part of my daily routine. Being a video game collector also keeps me occupied every now and then. To give everyone a slightly more personal look into my life, I'll update things such as what I'm currently listening to for the week and maybe game pickups over on <a href='/#/personal'>personal</a>. Who knows, maybe some guitar covers will start popping up along the way too.","profProject1":"The website youre currently viewing is my first stamp in the web development world but certainly not the last. After spending some time learning the basics of web development, I thought there wouldnt be a better way to put my skills to the test than to create my own website. Not only does this project showcase what I have been learning, it also serves as the beginning of my portfolio and hopefully ticket for landing myself a job.","profProject2":"Worked with a team of eight to implement a modern parking garage/lot system. My responsibilities included the programming of hardware stations in C++ to simulate a working environment which communicated with our database. I also wrote Python scripts which pushed data to the Firebase database to enhance and speed up the testing of the system on every platform. This projected was awarded <a target=\"_blank\" href=\"https://www.facebook.com/unlvengineering/photos/a.1520621151290225/1520632724622401/?type=3&amp;theater\">2nd Place</a> in the Computer Science Senior Design Category at UNLV.","profProject3":"This project focused on the implementation of a loadshare algorithm which involved creating a SCADA system. My responsibilities included interfacing a Real-Time Automation Controller with multiple power devices on the microgrid to create logs and store them on a local server. A PC startup script in Python was created to parse these files and push them to a database using MySQL. This projected was awarded <a target=\"_blank\" href=\"https://www.facebook.com/unlvengineering/photos/a.1520621151290225/1520632404622433/?type=3&amp;theater\">2nd Place</a> in the Electrical and Computer Engineering Senior Design Category at UNLV.","profSkillSum1":"My programming skillset began with C++ and OOP during my undergraduate studies. This evolved into including scripting languages such as Python to go along with embedded systems. Everything I learned while obtaining my degree was enough to make an easy transition to the web development world and starting front end development with JavaScript, HTML, and CSS.","profSkillSum2":"VS Code is my go to IDE and I'm currently in the process of trying to come up with web applications and features to implement on this site that would be interesting and beneficial to learn at the same time."}

